import React, { useState, useEffect, lazy } from 'react';
import { Card, CardBody, Button, CardTitle, Table } from 'reactstrap';
import { useDispatch, useSelector } from 'react-redux'
import { getPackage } from '../../redux/actions'
import { imgURL } from '../../redux/config';
import { COLORS } from '../../colors'
import AvatarImage from '../../assets/images2/avatar.png'
import { editPackage } from '../../redux/actions';
const PackageForm = lazy(() => import("./packageForm"));


function Package() {

  const dispatch = useDispatch()
  const packageListSelector = useSelector((state) => state.package.packageList);
  const [packageList, setPackageList] = useState([])
  const [selectedDetails, setselectedDetails] = useState(null)

  // console.log("packageListSelector---------------------", packageListSelector)

  // console.log(packageList)


  useEffect(() => {
    if (!packageListSelector.length) {
      dispatch(getPackage())
    } else {
      if (packageListSelector.length && packageListSelector.length !== packageList.length) {
        setPackageList(packageListSelector)
      }
    }
  }, [packageListSelector.length])



  const updateStatus = (item) => {

    let obj = {
      name: item.name,
      status: item.status === "active" ? "disable" : "active"
    }
    dispatch(editPackage(item._id, obj, (data) => {
      setPackageList(data)

    })  
    )
  }

  const renderTable = () => (

    
    <Card>
      <CardTitle className="bg-light border-bottom p-3 mb-0">
        <i className="mdi mdi-border-none mr-2"> </i>
        Category List
      </CardTitle>

      <CardBody className="">
        <Table responsive style={{ textTransform: "capitalize" }}>
          <thead>
            <tr style={{ textAlign: 'center' }}>
              <th>#</th>
              <th >Image Cover</th>
              <th>Name</th>
              {/* <th>Category</th> */}
              <th>Status</th>
              <th>Action</th>

            </tr>
          </thead>
          <tbody>
            {packageList.map((item, index) => (
              <tr style={{ textAlign: 'center', height: 60 }}>
                <th scope="row">1</th>
                <td>
                  {console.log("my image..", imgURL + item.image)}
                  <img src={item.image ? imgURL + item.image : AvatarImage} style={{ width: 100, height: 60, marginTop: -5, resize: 'contain' }} />
                </td>
                <td>{item.name}{item.recommended && (<><br /><span style={{ background: COLORS.primary06, fontSize: 12, paddingLeft: 5, borderRadius: 100, paddingRight: 5, color: 'white' }}> Recommended </span></>)} </td>
                {/* <td>{item.packageTypeId.name} </td> */}
                <td>{item.status}</td>

                <td style={{ display: "flex", alignItems: 'center' }}>
                  <Button className='btn btn-info'
                    onClick={() => updateStatus(item)}
                    style={{
                      backgroundColor: item.status === "active" ? COLORS.primary : COLORS.black,
                      borderColor: item.status === "active" ? COLORS.primary : COLORS.black,
                      width: '80px'
                    }} >{item.status === "active" ? "Disable" : "Active"}</Button>

                  <Button

                    onClick={() => setselectedDetails(item)}
                    style={{ width: '80px', marginTop: 5, backgroundColor: COLORS.black06, borderColor: COLORS.black06 }}
                    className="btn btn-info ml-3">Edit</Button>
                </td>
              </tr>

            ))}
          </tbody>
        </Table>
      </CardBody>
    </Card>
  )
  return (<div>

    <PackageForm editData={selectedDetails}
      cancelEdit={() => setselectedDetails(null)}
      updateList={(data) => setPackageList(data)} />
    {renderTable()}
  </div>);
}

export default Package;
